.help {
    background-color: #fafafa;
    padding: 60px 0 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    @include b(mobile) {
        padding: 40px 0;
    }

    &__caption {
        text-align: center;
        margin-bottom: 75px;
        @include b(mobile) {
            margin-bottom: 50px;
        }
    }

    &__icons {
        display: flex;
        justify-content: center;
        // align-items: center;
        flex-wrap: wrap;
    }

    &__items {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 20%;
        padding: 38px 10px;
        @include b(mobile) {
            width: 33.333%;
        }
        @include b(526) {
            padding: 38px 0;
        }

        img {
            max-width: 100%;
        }
    }

    &__img {
        display: flex;
        justify-content: center;
        margin-bottom: 24px;
        width: 26%;
        @include b(600) {
            width: 40px;
        }
    }

    &__descr {
        font-size: 20px;
        line-height: 1.2;
        @include b(467) {
            font-size: 13px;
        }
    }
}